@use 'base/variables' as v;
@use 'base/mixins' as m;

.footer {
    margin: 0rem;
    padding: 5rem 0;
    background-color: v.$colorFondoFooter;

    &__titulo {
        display: flex;
        flex-direction: column;
        align-items: center;
        width: 70%;
        margin: 0 auto;
        text-align: center;
    }

    &__redes {
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-around;
        gap: 1rem;
        padding: 0.4rem;
        margin-bottom: 2rem;
    }

    &__CopyBox {
        text-align: center;
    }
}

.redes {
    background-color: v.$colorFondoRed;
    color: v.$colorParrafoTres;
    width: 1rem;
    border-radius: 4px;
    padding: 0.4rem;
    text-decoration: none;

    &:hover {
        cursor: pointer;
    }
}