@use 'base/variables' as v;
@use 'base/mixins' as m;

.informacion {
    display: flex;
    flex-direction: row;
    position: relative;
    transition: right .7s ease;
    right: 0%;

    &.right100 {
        right: 100%;
    }

    &.right200 {
        right: 200%;
    }

    &.right300 {
        right: 300%;
    }

    span.material-symbols-outlined {
        padding: 0.6rem;
        border-radius: 6px;
        margin-bottom: 1rem;

        &#contact {
            background-color: #E9ECFE;
            color: #7186f8;
        }

        &#bolt {
            background-color: #F1F2F4;
            color: #7D8999;
        }

        &#brush {
            background-color: #FFEAED;
            color: #FF3755;
        }

        &#divices {
            background-color: #E8FAEE;
            color: #35D26A;
        }
    }

    & .material-symbols-outlined {
        font-variation-settings:
            'FILL' 0,
            'wght' 600,
            'GRAD' 0,
            'opsz' 48;
    }
}

.informacionSobremi,
.informacionServicio,
.informacionPortafolio,
.informacionContacto {
    background-color: #fff;
    min-width: 95%;
    width: 95%;
    margin: -10rem 2.5% 3rem 2.5%;

    @include m.telefonoXL {
        min-width: 85%;
        width: 85%;
        margin: -12rem 7.5% 3rem 7.5%;

    }

    position: relative;
    border-radius: .4rem;
    padding-bottom: 4rem;
    margin-top: -12rem;
    margin-bottom: 4rem;

    &__plantillaSobreGeneral {
        padding: 5rem 1rem 0rem 1rem;

        @include m.telefonoXL {
            padding: 5rem 4rem 0rem 4rem;

        }
    }

    &__plantillaSobre {
        display: flex;
        flex-direction: column;
        align-items: center;

        @include m.telefonoXL {
            text-align: center;
        }

        gap: 1rem;
        width: 100%;

        @include m.tablet {
            width: 85%;
        }

        margin: 0 auto;
        line-height: 1.6;
    }

    &__sobremiDatos {
        padding-bottom: 2rem;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        border-bottom: 1px solid v.$grisClaro;

        @include m.tablet {

            display: grid;
            grid-template-columns: repeat(2, 1fr);
        }
    }

    &__H4 {
        margin-top: 0;
        margin-bottom: 1rem;
    }

    &__H5 {
        margin: 0;
    }

    &__Skills {
        display: flex;
        flex-direction: column;
        gap: 0.5rem 2rem;
        padding-bottom: 4rem;
        border-bottom: 1px solid v.$grisClaro;

        @include m.tablet {
            display: grid;
            grid-template-columns: repeat(2, 1fr);
        }
    }

    &__experiencia {
        padding: 5rem 1rem 0rem 1rem;

        @include m.telefonoXL {
            padding: 5rem 4rem 0rem 4rem;

        }

        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 3rem;
    }

    // &__boxExperienciaTitulo {}

    &__boxExperienciaDetalles {
        display: flex;
        flex-direction: column;
    }

    &__boxExperienciaArticuloGrid {
        display: flex;
        flex-direction: column;
        align-items: flex-start;

        @include m.tablet {
            display: grid;
            grid-template-columns: 1fr 2fr;
            gap: 1rem;
        }
    }

    &__boxExperienciaArticulo {
        margin-bottom: .4rem;

        & .informacion__H4 {
            padding: 0;
            margin: 0;
            font-size: 1rem;
        }
    }

    &__cv {
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 2rem;
        background-color: v.$colorFondoBtn;
        padding: 80px .4rem;
        width: 100%;
        margin: 0 0 0rem 0rem;
        grid-column: 1/3;

        & .informacionSobremi__H3 {
            width: 100%;
            padding: 0;
            text-align: center;
        }
    }

    // &__cvTitulo {}

    &__cvDescarga {
        display: flex;
        flex-direction: column;
        gap: 1rem;

        @include m.telefonoXL {
            display: grid;
            grid-template-columns: 1fr 1fr;
        }
    }

    &__plantillaServicios {
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 1rem;

        @include m.telefonoXL {

            display: grid;
            grid-template-columns: 1fr 1fr;
            grid-row-start: 1fr 1fr;
            gap: 3rem;
        }

        h5 {
            margin-bottom: .6rem;
        }

    }

    &__muestrasGrid {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        grid-template-rows: repeat(6, auto);
        height: auto;
        width: 110%;
        gap: 1rem;
    }

    &__muestrasBox {
        width: 100%;
        display: flex;
        flex-direction: column;
        gap: .1rem;
        border-radius: 4px;

        &:hover {
            cursor: pointer;
        }

        p.informacionPortafolio__parrafo {
            text-align: start;
            margin-bottom: 0;
        }

        h4 {
            text-align: start;
        }
    }

    &__imagenPortafolioBox {
        overflow: hidden;
        height: 15rem;
        max-height: min-content;
        border-radius: 4px;
    }

    &__imagenPortafolio {
        width: 100%;
        height: 100%;
        border-radius: 4px;
        transition: transform .4s ease-in-out;

        &:hover {
            transform: scale(1.2);
        }
    }

    &__contactoInfoResGrid {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        align-items: flex-start;
        gap: 1rem;
        padding-bottom: 5rem;
        border-bottom: 1px solid v.$grisClaro;

        @include m.telefonoXXL {
            display: grid;
            grid-template-columns: repeat(3, 1fr);
            align-items: flex-start;
        }
    }

    &__contactoInfoResBox {
        flex-direction: column;
        align-items: flex-start;
        justify-content: space-around;
        gap: .2rem;

        @include m.telefonoXXL {
            flex-direction: column;
            align-items: flex-start;
            gap: .2rem;
        }
    }

    &__parrafoContacto {
        margin-bottom: .2rem;
    }

    &__formularioContacto {
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 1rem;
        margin-top: 3rem;

        @include m.telefonoXL {
            display: grid;
            grid-template-columns: repeat(2, 1fr);
            gap: 3rem;
        }
    }

    &__formularioInput {
        box-shadow: none;
        background-color: unset;
        height: 2rem;
        padding: 0.5rem 1rem;
        border: 1px solid lighten(v.$grisClaro, 10);
        border-radius: 6px;
        transition: border-color 0.2s ease-in-out;
        outline: none;
        width: 90%;

        @include m.telefonoXL {
            width: auto;
        }

        &:focus {
            border: 6px solid lighten(v.$colorBtn, 40);
            padding: 0.18rem .68rem;
        }
    }

    textarea {
        height: 10rem;
    }
}

#InputNombre {
    grid-column: 1/2;
}

#InputEmail {

    grid-column: 2/3;
}

#InputAsunto {
    grid-column: 1/3;

}

#Inputmensaje {
    grid-column: 1/3;

}

#BtnDownload,
#InputEnviar {
    background-color: v.$colorBtn;
}

#InputEnviar {
    width: 66.66%;
    margin: 0 auto;
    grid-column: 1/3;

    @include m.tablet {
        width: 33.33%;
    }
}

.barra {
    display: grid;
    grid-template-columns: 1fr 1px;
    align-items: center;
    column-gap: 1rem;

    margin: -.5rem 0;
    padding: 0;
    border-radius: 4px;
    background-color: lighten(v.$grisClaro, 10);

    &.temaClaro {
        background-color: darken(v.$colorBtn, 20);
    }

    :root {
        --habilidades-bar-lenght: 0%;
    }

    &__bar,
    &__bar::after {
        position: relative;
        background-color: v.$grisClaro;
        width: 100%;
        height: 5px;
        // border-radius: 8px;
        overflow: hidden;
    }

    &__bar::after {
        content: "";
        position: absolute;
        left: -100%;
        background-color: v.$colorBtn;
        transition: 0.3s ease-in-out;
        animation: habilidades-barra 3s ease-in-out forwards;
    }

    &__bar--0::after {
        --habilidades-bar-lenght: 0%;
    }

    &__bar--5::after {
        --habilidades-bar-lenght: 5%;
    }

    &__bar--10::after {
        --habilidades-bar-lenght: 10%;
    }

    &__bar--15::after {
        --habilidades-bar-lenght: 15%;
    }

    &__bar--20::after {
        --habilidades-bar-lenght: 20%;
    }

    &__bar--25::after {
        --habilidades-bar-lenght: 25%;
    }

    &__bar--30::after {
        --habilidades-bar-lenght: 30%;
    }

    &__bar--35::after {
        --habilidades-bar-lenght: 35%;
    }

    &__bar--40::after {
        --habilidades-bar-lenght: 40%;
    }

    &__bar--45::after {
        --habilidades-bar-lenght: 45%;
    }

    &__bar--50::after {
        --habilidades-bar-lenght: 50%;
    }

    &__bar--55::after {
        --habilidades-bar-lenght: 55%;
    }

    &__bar--60::after {
        --habilidades-bar-lenght: 60%;
    }

    &__bar--65::after {
        --habilidades-bar-lenght: 65%;
    }

    &__bar--70::after {
        --habilidades-bar-lenght: 70%;
    }

    &__bar--75::after {
        --habilidades-bar-lenght: 75%;
    }

    &__bar--80::after {
        --habilidades-bar-lenght: 80%;
    }

    &__bar--85::after {
        --habilidades-bar-lenght: 85%;
    }

    &__bar--90::after {
        --habilidades-bar-lenght: 90%;
    }

    &__bar--95::after {
        --habilidades-bar-lenght: 95%;
    }

    &__bar--100::after {
        --habilidades-bar-lenght: 100%;
    }
}

@keyframes habilidades-barra {
    to {
        transform: translateX(var(--habilidades-bar-lenght))
    }
}

.ocultar {
    z-index: -1;
    height: 0;
    overflow: hidden;
    height: 40rem;
}

.disp {
    transition: transform .3s ease-in-out;
    transform: scale(0);
    opacity: .7;
}

.bloc {
    transition: transform .3s ease-in-out;
    transform: scale(1);
    opacity: 1;
}

.scroll {
    overflow-y: hidden;
}