@use 'base/variables' as v;
@use 'base/mixins' as m;

.header {
    padding: .8rem 1.2rem;
    position: fixed;
    z-index: 1;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: max-content;
    background-color: transparent;
    left: 0;
    right: 0;
    height: 3rem;
    overflow: hidden;
    transition: height .3s ease-in-out, background-color .3s ease-in-out;

    &.activo {
        background-color: transparent;
        height: 11rem;
        background-color: #fff;

        h1,
        span {
            color: v.$colorParrafo;
        }
    }

    @include m.tablet {
        flex-direction: row;

        &.activo {
            height: 3rem;
            background-color: transparent;
        }
    }

    span {
        color: white;
        position: absolute;
        top: 1.5rem;
        right: 1rem;

        &.material-symbols-outlined {
            &:hover {
                cursor: pointer;
            }
        }

        @include m.telefonoXXL {
            display: none;
        }
    }

    &.headerScrollAct {
        background-color: #fff;

        h1,
        span {
            color: v.$colorParrafo;
        }

        transition: all .2s ease-in-out;
    }

}