@use 'base/variables' as v;
@use 'base/mixins' as m;

body {
    background-color: v.$colorFondoBody;
    user-select: none;
}


p {
    margin-top: 0;
    margin-bottom: 1.4rem;
}

.cuerpo {
    overflow-x: hidden;
}

.colorTitulo {
    color: v.$colorTitulo;
    font-family: v.$fuenteTitulo;
}

.colorTituloDos {
    color: v.$colorTituloDos;
    font-family: v.$fuentePrincipal;
}

.colorTituloTres {
    color: v.$colorTituloTres;
    font-family: v.$fuenteTitulo;
}

.colorLinks {
    color: v.$colorParrafo;
    font-family: v.$fuentePrincipal;
    font-size: 1rem;

    @include m.telefonoXXL {
        color: v.$colorLinks;
    }
}

.colorSubTitulo {
    color: v.$colorSubTitulo;
    font-family: v.$fuentePrincipal;
}

.colorParrafo {
    color: v.$colorParrafo;
    font-family: v.$fuenteParrafo;
}

.colorParrafoDos {
    color: v.$colorParrafoDos;
    font-family: v.$fuenteParrafo;
}

.colorParrafoTres {
    color: v.$colorParrafoTres;
    font-family: v.$fuenteParrafo;
}

.colorPaCopyright {
    color: v.$colorPaCopyright;
    font-family: v.$fuentePrincipal;
}

.btnGeneral,
.btnPortfolio {
    padding: .8rem 1rem;
    border-radius: 4px;
    border: none;

    &:hover {
        cursor: pointer;
    }
}

.btnPortfolio {
    padding: .4rem 1rem;
}

[class*="__H1"] {
    font-size: 2.5rem;
    font-weight: 500;
    padding: 0;
    margin: 0;
}

[class*="__H2"] {
    font-size: 2.2rem;
    font-weight: 500;
    line-height: 1.3;
    padding: 0;
    margin: 0;
}

[class*="__H3"] {
    font-size: 2rem;
    font-weight: 500;
    padding: 0;
    margin: 0;
}

[class*="__H4"] {
    font-size: 1.25rem;
    font-weight: 500;
    line-height: 1.5;
    padding: 0;
    margin: 0;
}

[class*="__H5"] {
    font-size: 1.2rem;
    font-weight: 600;
    line-height: 1.5;
    padding: 0;
}

.scrollTop {
    position: fixed;
    padding: 0.2rem;
    bottom: -3rem;
    right: 1rem;
    transition: background-color .15s ease-in-out,
        color .15s ease-in-out, bottom .15s ease-in-out;

    &.activo {
        bottom: 1rem;
    }

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    z-index: 2;

    background-color: v.$colorBtn;
    color: v.$colorTitulo;
    border-radius: 4px;

    &:hover {
        cursor: pointer;
        background-color: lighten(v.$colorBtn, 5);

    }


    &__span {
        // color: v.$colorParrafo;
        font-size: 2rem;
    }
}

.loguitoBox {}

.loguito {
    @include m.bgImage("/build/img/fondo-gales.png", v.$grisOscuroTransparente);
    display: flex;
    flex-direction: row;
    width: 50rem;
    height: 50rem;
    font-size: 26rem;
    align-items: center;
    justify-content: center;
    font-weight: 600;
    font-family: v.$fuenteTitulo;
    margin: 2rem auto;
    font-family: 'M PLUS Rounded 1c', sans-serif;

    // border-radius: 100%;
}