@use 'base/variables' as v;
@use 'base/mixins' as m;

.bienvenida {
    @include m.bgImage("/img/fondo-gales.png", v.$grisOscuroTransparente);
    position: relative;
    width: 100%;
    margin: 0 auto;
    padding-top: 14rem;
    padding-bottom: 10rem;
    top: -7rem;
    z-index: -1;

    &__box {
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    &__imagenBox {
        position: relative;
    }

    &__cuadroPerfil {
        margin: auto;
        background-image: linear-gradient(to right, #393E41 50%, #fff 50%, #fff 100%);
        width: 12rem;
        height: 12rem;
        border-radius: 100%;

    }

    &__imagenPerfil {
        display: block;
        width: 11rem;
        height: 11rem;
        margin-top: -11.5rem;
        margin-left: .5rem;
        align-items: center;
        border-radius: 100%;
        overflow: hidden;
    }

    &__titulo {
        margin-top: 2.2rem;
    }

    &__tituloOficio {
        font-size: 1.5rem;
        font-weight: 500;
        line-height: 1.3;
        padding: 0 auto;
        margin: 0 auto;
        text-align: center;
    }
}