@use 'base/variables' as v;
@use 'base/mixins' as m;



.informacionPortafolio__muestrasGrid {
    transition: grid-row 3s ease;

    & #article-1,
    & #article-3,
    & #article-5,
    & #article-8 {

        & .informacionPortafolio__imagenPortafolioBox,
        & .informacionPortafolio__imagenPortafolio {
            height: 20rem;
        }
    }



}

.informacionPortafolio__muestrasGrid {

    & #article-2,
    & #article-4,
    & #article-6,
    & #article-7,
    & #article-9 {

        & .informacionPortafolio__imagenPortafolioBox,
        & .informacionPortafolio__imagenPortafolio {
            height: 10rem;
        }
    }
}

#article-1 {

    &.all {
        display: block;
        grid-row: 1/3;
    }


    &.web {
        display: none;
    }

    &.branding {
        display: block;
        grid-row: 1/3;
    }

    &.design {
        display: none;
    }

}

