@use 'base/variables' as v;
@use 'base/mixins' as m;

.header__navegador {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: .2rem;
    margin-top: 1rem;

    @include m.telefonoXXL {
        flex-direction: row;
        align-items: center;
        gap: .2rem;
    }

    &Opcion {
        background-color: transparent;
        border: none;
        outline: none;
        cursor: pointer;
        position: relative;

        &.linInf {
            @include m.tablet {
                &::after {
                    content: "";
                    display: block;
                    position: absolute;
                    top: 1.7rem;
                    width: 1.25rem;
                    margin-left: auto;
                    margin-right: auto;
                    bottom: 0.725rem;
                    left: 0.75rem;
                    right: 0.75rem;
                    border-bottom: 3px solid currentColor;
                }
            }
        }

        &:hover {
            color: darken(v.$colorLinks, 10);
        }
    }

    &.activo {
        .header__navegadorOpcion {
            &:hover {
                color: v.$colorBtn;

                @include m.tablet {
                    color: darken(v.$colorLinks, 10);
                }
            }
        }

    }
}